@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;1,900&display=swap');


html, body, #root{
    color: rgb(255, 255, 255);
    background-color: rgb(219, 82, 77);
    font-family: 'Nunito';
    text-align: center;
    margin: 0;
    overflow: hidden;
    height: 100%;
}

button{
    border-color: rgb(255, 255, 255);
    color: rgb(219, 82, 77);
    background-color: rgb(255, 255, 255);
    border-radius: 4px;
    border-style: solid;
    height: 80px;
    width: 150px;
    margin: 1vw;
    font-size: 22px;
    font-weight: bold;
    box-shadow: 0px 6px 0px;
}


input{
    padding: 5px;
    margin: 0px 5px;
    border-color: rgb(255, 127, 127);
    color: rgb(0, 0, 0);
    background-color: rgb(255, 255, 255);
    outline: none;
    border-style: none;
    border-radius: 5px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button{
    -webkit-appearance: none;
    margin: 0;
}

h1{
    margin: 0
}

.MainContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50%;
}

#TimerContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 5vh;
}

#number{
    margin: 80px;
}

#minutes{
    margin-left: 10px;
    margin-right: 50px;
    font-size: 20px;
}

h1{
    width: 300px;
}




